<template><section><h1>PointCollection</h1>
<p>The <code v-pre>vc-collection-primitive-point</code> component is used to load a renderable collection of points.</p>
<h2>Example</h2>
<h3>Load PointCollection with <code v-pre>vc-collection-primitive-point</code></h3>
<h4>Preview</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-point :points="points"></vc-collection-primitive-point>
        <vc-collection-primitive-point>
          <template v-for="(polyline, index) of polylines">
            <template v-for="(position, subIndex) of polyline.positions">
              <vc-primitive-point :position="position" :key="'point' + index + 'position' + subIndex" :color="colorPoint" :pixelSize="32"></vc-primitive-point>
            </template>
          </template>
        </vc-collection-primitive-point>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
