<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>modelMatrix</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> The 4x4 transformation matrix that transforms each point from model to world coordinates.</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td><code v-pre>false</code></td>
<td><code v-pre>optional</code> For debugging only. Determines if this primitive's commands' bounding spheres are shown.</td>
</tr>
<tr>
<td>blendOption</td>
<td>Number</td>
<td></td>
<td><code v-pre>optional</code> The point blending option. The default is used for rendering both opaque and translucent points. However, if either all of the points are completely opaque or all are completely translucent, setting the technique to BlendOption.OPAQUE or BlendOption.TRANSLUCENT can improve performance by up to 2x.</td>
</tr>
<tr>
<td>points</td>
<td>Array</td>
<td><code v-pre>[]</code></td>
<td><code v-pre>optional</code> Specifies an array of point collections.The array object structure is a <a href="./#/zh/primitive/vc-primitive-point"><code v-pre>vc-primitive-point</code></a> component property.</td>
</tr>
</tbody>
</table>
